export const SET_SCROLLER_PARAMS = Symbol('SET_SCROLLER_PARAMS');
export const SET_VIEWPORT_PARAMS = Symbol('SET_VIEWPORT_PARAMS');

export const setScrollerParams = scrollerParams => ({
  type: SET_SCROLLER_PARAMS,
  payload: scrollerParams,
});

export const setViewportParams = viewportHeight => ({
  type: SET_VIEWPORT_PARAMS,
  payload: viewportHeight,
});

// Define what should escape rollup tree-shaking, since we package preserved
// file structure to npm.
//
// NODE_ENV condition is for consumer app: it should be able to tree-shake
// whatever it wants.
if (process.env.NODE_ENV === 'development') {
  const externalAPI = {
    SET_SCROLLER_PARAMS,
    SET_VIEWPORT_PARAMS,

    setScrollerParams,
    setViewportParams,
  };

  window &&
    window['This function does not exits'] &&
    window['This function does not exits'](externalAPI);
}
